.wp-block-create-block-image-block {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
	width: 200px;
	display: flex;
	margin: 0;
}

.imageBlock {
	width: 34.3rem;
	height: 25.4rem;
	align-self: center;
	margin: 0;
	border-radius: 2rem;
	margin: 2rem 2rem 0rem 1rem;
}

.pageImage {
	width: 100%;
	height: 100%;
	object-fit: cover;
	border-radius: 1rem;
}

@media screen and (min-width: 700px) {
	.pageImageWrapper {
		width: 48.6rem;
		height: 36rem;
	}
}
